body,
html {
  margin: 0;
  padding: 0;
  background: #25252B;
}
* {
  touch-action: manipulation;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

body {
  position: relative;
  color: white;
  letter-spacing: 2px;
  font-size: 11px;
  font-family: 'Poppins', sans-serif;
  width: 100%;
  height: 100vh;
}

#canvas {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  display: block;
}

#instructions {
  color:black;
  position:fixed;
  text-align:right;
  width: 100%;
  padding: 15px;
}